import { Paper, TableBody, TableCell, TableRow, TextField, Toolbar, withStyles, InputAdornment } from "@material-ui/core";
import React, { useState, useEffect, useRef } from "react";
import * as VActions from "../../../../../actions/Vehicles/vehicles";
import AddNewVehicleForm from "../AddVehicles/AddNewVehicleForm";
import { connect } from "react-redux";
import useTable from "../../useTable";
import Controls from "../../../controls/Controls";
import { EditOutlined, Search } from "@material-ui/icons";
import AddIcon from "@material-ui/icons/Add";
import PageHeader from "../../../PageHeader";
import CategoryTwoToneIcon from '@material-ui/icons/CategoryTwoTone';
import Popup from "../../../Popup";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import CloseIcon from "@material-ui/icons/Close";
import DeleteIcon from "@material-ui/icons/DeleteForeverSharp";
import { useToasts } from "react-toast-notifications";
import Notification from "../../../controls/Notification";
import ConfirmDialog from "../../../controls/ConfirmDialog";



const styles = theme => ({

    paper: {
        margin: theme.spacing(-8.9),
        padding: theme.spacing(6),

    },

    searchInput: {
        width: '75%'
    },

    newButton: {
        position: 'absolute',
        right: '10px '

    },
   
})

const headCells = [
    { id: 'regNo' , label: 'No.Plate' },
    { id: 'description', label: 'Description' },
    { id: 'Actions', label: 'Actions', disableSorting: true },
]

const ViewAllVehicles = ({ classes, ...props }) => {

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [openPopup, setOpenPopup] = useState(false)
    const [currentId, setCurrentId] = useState(0)
    const { addToast } = useToasts()
    const {notify, setNotify} = useState({isOpen:false, message:'', type:''})
    const [confirmDialog, setConfirmDialog] = useState(false) 
    const [deleteRecord, setDeleteRecord] = useState(false)

    const showToast= () =>{
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })

    }
    const onDelete = id => {
            props.DeleteVehicle(id, showToast )
            setConfirmDialog(false)

    }
    

    useEffect(() => {
        props.fetchAllVehicles();

       

    }, []);




    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting,
    } = useTable(props.vehiclesList, headCells, filterFn)


    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value == "")
                    return items;
                else {
                    return items.filter(x =>

                        x.regNo.toLowerCase().includes(target.value.toLowerCase()) +
                        x.description.toLowerCase().includes(target.value.toLowerCase()))

                }




            }
        })
    }

    const addorEdit = (employee, resetForm) => {

        setOpenPopup(false)
        setNotify({
            isOpen:true,
            message:'Submitted Successfully',
            type: 'Success'
        })
    }

    const openInPopup = record => {
        setCurrentId(record)
        setOpenPopup(true)
    }

    const openInDialog = () => {
        setConfirmDialog(true)
        
        
        
    }

    return (

        <>
            <PageHeader
                title="Vehicles List"
                //subTitle="Form design with validation"
                icon={<CategoryTwoToneIcon  fontSize="large" />}
            />,
            <Paper className={classes.paper} elevation={3}>

                <Toolbar>
                    <Controls.Input
                        className={classes.searchInput}
                        label="Search for vehicle"

                        InputProps={{
                            startAdornment: (<InputAdornment position="start">
                                <Search />
                            </InputAdornment>)
                        }}
                        onChange={handleSearch}

                    />
                    <Controls.Button
                        text="Add New"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        className={classes.newButton}
                        onClick={() => {setOpenPopup(true); setCurrentId(0);}}
                    />
                </Toolbar>
                <table class="table .table-responsive{-sm|-md|-lg|-xl|-xxl} table-hover " >
                    <thead >


                    </thead>
                    <tbody>
                        <TblContainer >
                            <TblHead />
                            <TableBody>
                                {
                                    recordsAfterPagingAndSorting().map((record, index) => {
                                        return (<TableRow key={index} hover>
                                            <TableCell>{record.regNo}</TableCell>
                                            <TableCell>{record.description}</TableCell>
                                            
                                            <TableCell>
                                                <Controls.ActionButton
                                                    color="primary"
                                                    // onClick={()=>{setCurrentId(record.id)}}
                                                    onClick={()=> {openInPopup(record.regNo)}}
                                                >
                                                    <EditOutlinedIcon fontSize="small" />
                                                </Controls.ActionButton>
                                                <Controls.ActionButton
                                                    color="secondary"
                                                    onClick={() => {
                                                        openInDialog()
                                                        setConfirmDialog({
                                                            onConfirm: () =>{onDelete(record.regNo)}
                                                        })
                                                        
                                                        //onDelete(record.id)
                                                    }
                                                    }
                                                >
                                                    <DeleteIcon fontSize="small" />
                                                </Controls.ActionButton>
                                            </TableCell>
                                        </TableRow>

                                        )
                                    })
                                }

                            </TableBody>
                        </TblContainer>


                    </tbody>
                </table>

                <TblPagination />
            </Paper>
            <Popup
                title="Category Form"
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
            >
                <AddNewVehicleForm
                {...({ currentId, setCurrentId, setOpenPopup }) }
                    //addorEdit={addorEdit}
                />
            </Popup>
            <Notification
            notify={notify}
            setNotify ={setNotify} 
            />
            <ConfirmDialog 
            title="Are you sure you want to delete this record?"
            subTitle="This operation can't be reversed"
            confirmDialog={confirmDialog}
            setConfirmDialog = {setConfirmDialog}
            />
        </>
        // {...({ currentId, setCurrentId }) }

    )
}

const mapStateToProps = state => ({
    vehiclesList: state.vehicles.list
    
})

const mapActionToProps = {
    fetchAllVehicles: VActions.fetchAllVehicles,
    DeleteVehicle: VActions.DeleteVehicle,
    
}


export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(ViewAllVehicles));