import React, { Fragment, useState, useEffect } from "react";
import { FormControl, Grid, TextField, withStyles, InputLabel, Select, MenuItem, Button, FormHelperText } from "@material-ui/core";
import { useForm } from "../../useForm";
import { connect } from "react-redux";
//import * as actions from "../../../../../actions/Staff/addStaff";
import * as VActions from "../../../../../actions/Vehicles/vehicles";
//import * as userLevelActions from "../../../../../actions/UserLevel/userLevel";
import { ToastConsumer, useToasts } from "react-toast-notifications";
import "../../staffForm.css";
import { RestoreSharp } from "@material-ui/icons";
import Popup from "../../../RPopup ";
import ErrorDialog from "../../../controls/errorDialog";



const styles = theme => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            minWidth: 230,
        }
    },

    formControl: {
        margin: theme.spacing(1),
        minWidth: 230,
    },
    smMargin: {
        margin: theme.spacing(1),

    },
})

const initialFieldValues = {

    regNo: '',
    description: '',
    // phone: '',
    // location: '',
    // // email: '',
    // // address: '',
    // companyDataId: '',


}






const AddNewVehicleForm = ({ classes, ...props }) => {

    //toast message
    const { addToast } = useToasts();
    const [openPopup, setOpenPopup] = useState(false);
    const [errorDialog, setErrorDialog] = useState(false);
    const [tittle, setTittle] = useState("");

    //realtime validation for the whole form controls
    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('regNo' in fieldValues) {
            temp.regNo = fieldValues.regNo ? "" : "This field is required"
            // addToast("User data saved succeffuly", {appearance:'success'})
            // console.log("Tast has beeen displayed")
        }


        if ('description' in fieldValues) {
            temp.description = fieldValues.description ? "" : "This field is required"
        }






        setErrors({
            ...temp
        })
        if (fieldValues == values) {
            return Object.values(temp).every(x => x == "")
        }


    }

    const [
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    ] = useForm(initialFieldValues, validate, props.setCurrentId);

    const showToast = () => {
        addToast("Vehicle data saved succeffuly", { appearance: 'success' })
        addToast("Vehicle data saved succeffuly", { appearance: 'success' })
        addToast("Vehicle data saved succeffuly", { appearance: 'success' })
        props.fetchAllVehicles();
    }

    const openInErrorDialog = () => {
        setErrorDialog(true)



    }

    const handleSubmit = e => {
        e.preventDefault()


        if (validate()) {

            const onSuccess = () => {
                showToast();
                resetForm();


            }



            if (props.currentId == 0) {

                //using toast notification

                props.createVehicle(values, onSuccess)
                //addorEdit(values, onSuccess)

            }
            else {
                //using windows alert notification
                //props.updateDCandidate(props.currentId, values, () => {window.alert('updated')})
                //using toast notification
                if (values.regNo == props.currentId) {
                    props.updateVehicle(props.currentId, values, onSuccess)

                } else {
                    console.log('No Plate cannot be updated. It may be associated with Many transactions');
                    setTittle("Vehicle No.Plate cannot be updated. It may be associated with Many transactions.");
                    openInErrorDialog();
                    setErrorDialog({
                        onConfirm: () => {  }
                    })

                    resetForm();

                }




            }

            if (props.openPopup == true) {
                props.setOpenPopup(false)
            }

        }


    }

    useEffect(() => {


        if (props.currentId != 0)
            setValues({
                ...props.vehiclesList.find(x => x.regNo == props.currentId)
            })
        setErrors({})
        props.fetchAllVehicles();
        // props.fetchAllUserLevel();



    }, [props.currentId])





    return (
        <>
            <section className="content">
                <div className="container-fluid">
                    <div className="row">
                        {/* left column */}
                        <div className="col-md-12">
                            {/* jquery validation */}
                            <div className="card card-primary">
                                <div className="card-header">
                                    <h4 className="card-title">Add Vehicles <small> </small></h4>
                                </div>
                                {/* /.card-header */}
                                {/* form start */}
                                <form id="quickForm" autoComplete="off" noValidate className={classes.root} onSubmit={handleSubmit}>

                                    <TextField
                                        className="form-ctr"
                                        id="standard-basic"
                                        type="text"
                                        name="regNo"
                                        label="Number Plate"
                                        value={values.regNo}
                                        onChange={handleInputChange}
                                        {...(errors.regNo && { error: true, helperText: errors.regNo })}
                                    />

                                    <TextField className="form-ctr" name="description" label="Description" value={values.description}
                                        onChange={handleInputChange}


                                        {... (errors.description && { error: true, helperText: errors.description })}
                                    />






                                    {/* /.card-body */}
                                    <div className="card-footer">
                                        <div>
                                            <button type="submit" className="btn btn-primary" >Save</button>
                                            <button variant="contained"
                                                className="btn btn-secondary"
                                                onClick={resetForm} style={{ marginLeft: 3 }}>Reset</button>
                                        </div>

                                    </div>

                                </form>

                            </div>
                            {/* /.card */}
                        </div>
                        {/*/.col (left) */}
                        {/* right column */}
                        <div className="col-md-6">
                        </div>
                        {/*/.col (right) */}
                    </div>
                    {/* /.row */}
                </div>{/* /.container-fluid */}
            </section>

            <ErrorDialog
                title={tittle}
                // subTitle="This operation can't be reversed"
                errorDialog={errorDialog}
                setErrorDialog={setErrorDialog}
            />
        </>

    );
}


const mapStateToProps = state => ({

    vehiclesList: state.vehicles.list

})



const mapActionToProps = {
    createVehicle: VActions.createVehicle,
    updateVehicle: VActions.updateVehicle,
    fetchAllVehicles: VActions.fetchAllVehicles,
    //fetchAllUserLevel: userLevelActions.fetchAllUserLevel,
    //fetchAllAddStaff: actions.fetchAll,

}
export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(AddNewVehicleForm));